.ui-animate {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.ui-animate.ui-animate-infinite {
  animation-iteration-count: infinite;
}

.ui-animate.ui-animate-hinge {
  animation-duration: .3s;
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -30px, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

.ui-loader {
  font-size: 10px;
  position: relative;
  margin: 0 auto;
  text-indent: -9999em;
  border-radius: 100%;
  width: 0.8em;
  height: 0.8em;
  animation-delay: -0.16s;
  animation-fill-mode: both;
  animation: loader 1.3s infinite ease-in-out;
}

.ui-loader:before, .ui-loader:after {
  content: '';
  position: absolute;
  top: 0;
  border-radius: 100%;
  width: 0.8em;
  height: 0.8em;
  animation-fill-mode: both;
  animation: loader 1.3s infinite ease-in-out;
}

.ui-loader:before {
  left: -1.5em;
  animation-delay: -0.2s;
}

.ui-loader:after {
  left: 1.5em;
  animation-delay: 0.2s;
}

@keyframes loader {
  0%,
  80%,
  100% {
    box-shadow: 0 0.8em 0 -0.8em #fff;
  }
  40% {
    box-shadow: 0 0.8em 0 0 #fff;
  }
}

@keyframes loader-red {
  0%,
  80%,
  100% {
    box-shadow: 0 0.8em 0 -0.8em red;
  }
  40% {
    box-shadow: 0 0.8em 0 0 red;
  }
}

@keyframes loader-night-red {
  0%,
  80%,
  100% {
    box-shadow: 0 0.8em 0 -0.8em #862718;
  }
  40% {
    box-shadow: 0 0.8em 0 0 #862718;
  }
}

.ui-loader-two {
  position: relative;
  display: inline-block;
  width: 8px;
  height: 18px;
  animation: spinnermove 1.5s infinite ease-in-out;
  transform: translateZ(0);
}

.ui-loader-two:after, .ui-loader-two:before {
  content: '';
  position: absolute;
  left: 0;
  background-color: #ed3010;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  animation: cubemove 1.5s infinite ease-in-out;
}

.ui-loader-two:before {
  top: 0;
}

.ui-loader-two:after {
  bottom: 0;
  animation-delay: 0.2s;
}

@keyframes cubemove {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes spinnermove {
  0% {
    transform: rotate(0deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(180deg);
    transform-origin: 50% 50%;
  }
}

