  width: 100%;
  color: #fff;
  background: rgba(136, 136, 136, 0.4);
  line-height: 40px;
  text-indent: 15px;
  text-align: left;
  font-size: 16px;
  z-index: 100;
  opacity: 1;
  transition: opacity 1s, transform 1s;
}

.ui-video-ad-control {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 40px;
  width: 100%;
  background: rgba(136, 136, 136, 0.4);
  z-index: 100;
  overflow: hidden;
  opacity: 1;
  transition: opacity 1s, transform 1s;
}

.ui-video-ad-control-box {
  position: relative;
  color: #fff;
  padding: 0 90px;
}

.ui-video-ad-control-box .ui-video-ad-control-duration,
.ui-video-ad-control-box .ui-video-ad-control-time {
  position: absolute;
  top: 0;
  width: 40px;
  height: 40px;
  line-height: 40px;
}

.ui-video-ad-control-box .ui-video-ad-control-duration {
  right: 40px;
}

.ui-video-ad-control-box .ui-video-ad-control-time {
  left: 40px;
}

.ui-video-ad-control-box .ui-video-ad-bar {
  top: 19px;
}

.ui-video-ad-control-play {
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../images/video.png");
  background-size: 118px 85px;
  background-position: -45px 0;
  width: 40px;
  height: 40px;
}

.ui-video-ad-control-exit {
  position: absolute;
  right: 0;
  top: 0;
  background-image: url("../images/video.png");
  background-size: 118px 85px;
  background-position: 0 -45px;
  width: 40px;
  height: 40px;
}

.ui-tooltips {
  position: relative;
  overflow: hidden;
}

.ui-tooltips-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  text-align: center;
  color: #929292;
  z-index: 1000;
  transform: translate(100%);
  animation-duration: .15s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
}

.ui-tooltips-content > .ui-close {
  position: absolute;
  right: 10px;
  bottom: 10px;
  color: #b1b1b1;
}

.ui-tooltips-in {
  animation-name: slideInRight;
}

.ui-tooltips-out {
  animation-name: slideOutRight;
}

.ui-icon:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
}

.ui-icon-dot:before {
  background: url("../images/icon.png") no-repeat;
  background-size: 54px 54px;
  background-position: -34px -42px;
  width: 15px;
  height: 4px;
}

.ui-icon-map:before {
  background: url("../images/icon.png") no-repeat;
  background-size: 54px 54px;
  background-position: 0px -42px;
  width: 9px;
  height: 12px;
}

.ui-icon-pic:before {
  background: url("../images/icon.png") no-repeat;
  background-size: 54px 54px;
  background-position: 0px -27px;
  width: 11px;
  height: 10px;
}

.ui-icon-play:before {
  background: url("../images/icon.png") no-repeat;
  background-size: 54px 54px;
  background-position: 0px 0px;
  width: 22px;
  height: 22px;
}

.ui-icon-talk:before {
  background: url("../images/icon.png") no-repeat;
  background-size: 54px 54px;
  background-position: -43px 0px;
  width: 11px;
  height: 10px;
}

.ui-night .ui-icon-talk:before {
  background-position: -16px -27px;
}

.ui-night .ui-icon-pic:before {
  background-position: -27px 0px;
}

.ui-night .ui-icon-map:before {
  background-position: -43px -15px;
}

.ui-night .ui-icon-dot:before {
  background-position: -14px -42px;
}

.ui-backflow-active .special-tools {
  bottom: 88px;
}

.ui-backflow-active .ui-backflow {
  display: block;
}

.ui-backflow {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.83);
  padding: 12px 0 12px 9px;
  z-index: 1010;
}

.ui-backflow:before, .ui-backflow:after {
  content: " ";
  display: table;
}

.ui-backflow:after {
  clear: both;
}

.ui-backflow-icon {
  float: left;
  margin-right: 7px;
}

.ui-backflow-icon img {
  width: 32px;
  height: 32px;
}

.ui-backflow-text {
  float: left;
}

.ui-backflow-text .ui-backflow-title {
  color: #fff;
  font-size: 1.4rem;
  line-height: 18px;
  height: 18px;
  vertical-align: text-top;
  font-weight: bold;
}

.ui-backflow-text .ui-backflow-desc {
  color: #8a8a8a;
  font-size: 10px;
  height: 14px;
  line-height: 16px;
}

.ui-backflow-btn {
  float: right;
  height: 28px;
  background: #59d141;
  color: #fff;
  border-radius: 2px;
  line-height: 28px;
  font-size: 14px;
  margin-right: 4px;
  margin-top: 2px;
  padding: 0 15px;
}

.ui-backflow-close {
  float: right;
  width: 32px;
  height: 32px;
  background-image: url("../images/close.png");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 11px 11px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
  .ui-backflow-btn {
    padding: 0 10px;
  }
  .ui-backflow-title {
    font-size: 1.2rem;
  }
}

.dropload-up,
.dropload-down {
  position: relative;
  height: 0;
  overflow: hidden;
  /* 开启硬件加速 */
  transform: translateZ(0);
}

.dropload-down {
  height: 60px;
}

.dropload-refresh,
.dropload-update,
.dropload-load,
.dropload-noData {
  height: 60px;
  padding: 10px;
  font-size: 10px;
  text-align: center;
}

.dropload-load .loading {
  display: inline-block;
  height: 15px;
  width: 15px;
  border-radius: 100%;
  margin: 6px;
  border: 2px solid #666;
  border-bottom-color: transparent;
  vertical-align: middle;
  animation: rotate 0.75s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
